import Col from 'react-bootstrap/Col';
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import { useCart } from "react-use-cart";

export default function YokohamaItem(props) {

    const { addItem } = useCart();

    const addToCart=()=>{
        addItem(props.brandYoko);
    }
    return (
        <>
          
                <Col md={3} lg={3} xs={12} className='product-container'>

                    <div className='product-thumb'>
                        <Link to={"/Yokohama/" + props.brandYoko.id}>
                            <img className="product-image" src={props.brandYoko.images}></img>
                        </Link>

                        <div className='product-info'>
                        <div>
                                <img className="product-logo"src={props.brandYoko.images1}></img>
                            </div>
                            <div className='product-title'>
                                {props.brandYoko.title}
                            </div>
                            <div className='product-description'>
                                {props.brandYoko.description}
                            </div>
                            <div className='product-price'>
                                $ {props.brandYoko.price}
                            </div>
                        </div>

                        <Button onClick={addToCart} style={{width:"100%",background:"#0d4e9d"}}>Add to Cart</Button>
                    </div>



                </Col>
           

        </>
    )
}