import './SignUp.css';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Link } from 'react-router-dom';
import { setSignupDetail, setSignupSuccess } from '../../../Redux/action/action';
import { connect } from "react-redux/es/exports";
import { useNavigate } from "react-router-dom";
import { useState } from 'react';

 function SignUp(props){
const navigate=useNavigate();




const setName=(e)=>{
    let signupDetail=props.signupDetail;
    signupDetail.name=e.target.value;
    setSignupDetail(signupDetail)
}
const setEmail=(e)=>{
    let signupDetail=props.signupDetail;
    signupDetail.email=e.target.value;
    setSignupDetail(signupDetail)
}
const setSignupNumber=(e)=>{
    let signupDetail=props.signupDetail;
    signupDetail.number=e.target.value;
    setSignupDetail(signupDetail)
}
const setPassword=(e)=>{
    let signupDetail=props.signupDetail;
    signupDetail.password=e.target.value;
    setSignupDetail(signupDetail)
}

const signUp=()=>{
    let signID={
        ...props.signupDetail,
        id:1
    }
    fetch("http://localhost:3000/signUp",{
        method:"POST",
    body: JSON.stringify(signID)
    }).then(res=>res.json()).then((res)=>{
        console.log(res);
        props.setSignupSuccess(true);
        localStorage.setItem("isSignedUp",true)
        // localStorage.setItem("user",JSON.stringify(res))
        localStorage.setItem("user",JSON.stringify(props.signupDetail))
        navigate("/")
    })
}
    return(
        <>
        <div className="login-container">
                <Container className='sub-container'>
                    <Row className="login-form">
                        <Col xs={12} md={12} >
                            <span style={{ fontWeight: '400', fontSize: '28px', marginTop: '0%' }}>Create Account</span>
                            <Form onSubmit={(f)=>{
                                f.preventDefault()
                            }}>
                                <Form.Group className="mb-3" controlId="formBasicName">
                                    <Form.Label>Your Name</Form.Label>
                                    <Form.Control onChange={(e)=>{
                                        setName(e)
                                    }} value={props.signupDetail.name}  type="text" >
                                        
                                    </Form.Control>
                                </Form.Group>

                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <Form.Label>Email Address</Form.Label>
                                    <Form.Control onChange={(e)=>{
                                        setEmail(e)
                                    }} value={props.signupDetail.email}type="email"  />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="formBasicNumber">
                                    <Form.Label>Mobile Number</Form.Label>
                                    <Form.Control onChange={(e)=>{
                                        setSignupNumber(e)
                                    }} value={props.signupDetail.number} type="tel"  />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="formBasicPassword">
                                    <Form.Label>Password</Form.Label>
                                    <Form.Control onChange={(e)=>{
                                        setPassword(e)
                                    }} value={props.signupDetail.password} type="password"  />
                                </Form.Group>
                                <span className='dat_sm'>
                                Passwords must be at least 8 characters.
                                </span>
                               <div>
                               <span className='dat_sm'>
                               We will send you a text to verify your phone.
                                </span>
                               </div>
                                
                                <Button onClick={signUp}className="btn-signup" variant="primary" type="submit">
                                    Sign Up
                                </Button>
                                    
                                <div className='tbs-divider-signup'>
                                <p>Already have an account ? 
                                <a href="/login" style={{textDecoration:"none"}} >&nbsp;Login</a>
                                </p>
                                <p>Having trouble signup? Call us 
                                <a href="tel:+919999823205" style={{textDecoration:"none"}}> &nbsp;+919999823205</a>
                                </p>
                                </div>

                            </Form>
                        </Col>

                    </Row>
                </Container>
            </div>

        </>
    )
} 
const mapStateToProps=(state)=>{
    return{
 signupDetail:state.productFeature.signupDetail
 
    }
}
const mapDispatchToProps=(dispatch)=>{
    return{
  setSignupDetail:(payload)=>dispatch(setSignupDetail(payload)),
  setSignupSuccess:(payload)=>dispatch(setSignupSuccess(payload)),
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(SignUp); 
                                    
                                
                               