import "./MobileVerification.css";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { useNavigate } from "react-router-dom";

export default function MobileVerification() {
    const navigate=useNavigate();

  return (
    <>
      <div className="outer-div">
        <Container className="mobile-container">
          <Col md={12} xs={12} lg={12} className="mobile-col">
            <div className="inner-div">
              <h2>Mobile Verification</h2>
              <p style={{float:"left"}}> Please Enter OTP.</p>
              <input type="text" id="user-otp" class="form-control" name="User[otp]" value="" placeholder="Enter OTP Sent You Mobile No"/>
              <p class="help-block help-block-error"></p>
              <div>
              <button onClick={()=>{
                navigate("/")
              }}type="submit" class="btn btn-default
			                     verificationbtn" name="verification">Verify</button>
                                  <div style={{float:"right"}}>
              <button type="submit" class="otp_resend">Resend OTP </button>
              </div>
              </div>
             
             
            </div>
          </Col>
        </Container>
      </div>
    </>
  );
}
