import { useEffect, useState } from "react";
import { useParams } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import ApolloImg from "../../../common/Components/atom/ApolloItem/ApolloImg";
import ApolloHeadDetail from "./ApolloHeadDetail";
import ApolloReview from "./ApolloReview";


export default function ApolloDetails() {
    const [tyreDetail, setTyreDetail] = useState(null);
    const [head, setHead] = useState('');
    const [data, setData] = useState('');
    const { id } = useParams();

    useEffect(() => {
        fetch("http://localhost:3000/apollo/" + id)
            .then(res => res.json()).then((res) => {
                setTyreDetail(res)
            })
    }, [])

    useEffect(() => {
        fetch("http://localhost:3000/apollo/" + id)
            .then(res => res.json()).then((res) => {
                setHead(res)
            })
    }, [])
    useEffect(() => {
        fetch("http://localhost:3000/apollo/" + id)
            .then(res => res.json()).then((res) => {
                setData(res)
            })
    }, [])

    return (
        <>
            <Container>
                <Row>
                    {
                        <ApolloHeadDetail apollo={head}></ApolloHeadDetail>
                    }
                </Row>

                <Row>
                    {
                        tyreDetail &&
                        <ApolloImg apollo={tyreDetail}></ApolloImg>
                    }
                </Row>
                <Row>
                    {
                        data &&
                        <ApolloReview apollo={data}></ApolloReview>
                    }
                </Row>
            </Container>
        </>
    )
}