import { Col, Container, Row, Table } from "react-bootstrap";
import { BsFillStarFill } from "react-icons/bs";
import Button from 'react-bootstrap/Button';
import { BsFillCartFill } from "react-icons/bs";

export default function BridgestoneImg(props){

    return(
        <>
        <Container style={{marginTop:"20px"}}>
            <Row>
            <Col md={4} xs={4}>
          {
            <img style={{width:"80%",height:"auto"}} src={props.brandBridgestone.images}></img>
          }
        </Col>
        <Col md={4} xs={4}>
            {
                <>
                <img style={{width:"35%",height:"auto",marginLeft:"65px",padding:"5px"}}src={props.brandBridgestone.images1}></img>
                <h4>{props.brandBridgestone.title} {props.brandBridgestone.description}</h4>
                <hr></hr>
              
               <div style={{paddingBottom:"5px"}}>
                <span style={{fontSize:"20px"}}>M.R.P.:</span>
                <span style={{marginLeft:'150px',fontSize:"20px",fontWeight:"500"}}>Rs. &nbsp;{props.brandBridgestone.price}</span>
               </div>
               <div style={{paddingBottom:"5px"}}>
                <span style={{fontSize:"18px"}}>Price:</span>
                <span style={{marginLeft:'170px',fontSize:"15px",color:"red"}}>Rs. &nbsp;{props.brandBridgestone.price}</span>
               </div>
               <div style={{paddingBottom:"5px"}}>
                <span>Inclusive of all taxes</span>
               </div>
               <div style={{paddingBottom:"5px"}}>
                <span><BsFillStarFill/><BsFillStarFill/><BsFillStarFill/><BsFillStarFill/><BsFillStarFill/></span>
                <span style={{marginLeft:'110px',fontSize:"15px"}}>0 out of 5 Star	</span>
               </div>
                <div style={{paddingBottom:"5px"}}>
                <span style={{fontWeight:"500"}}>Stock Level:</span>
                <span style={{marginLeft:'150px',fontSize:"15px"}}>In Stock</span>
               </div>
               <div>
               <Button className="cart_btn" variant="primary"><BsFillCartFill/>  Add to Cart</Button>
               </div>

                </>
                
            }
        </Col>
       
            <Col md={4} xs={4}>
          {
            <img style={{width:"100%",height:"auto"}} src={props.brandBridgestone.images2}></img>
          }
        </Col>
            </Row>
        </Container>
       
        
        </>
    )
}