import { SET_CART_DETAIL, SET_LOGIN_DETAIL, SET_LOGIN_EMAIL, SET_LOGIN_PASSWORD, SET_LOGIN_SUCCESS, SET_PRODUCT_DETAILS, SET_PRODUCT_FEATURES, SET_PRODUCT_LIST ,SET_SIGNUP_DETAIL, SET_SIGNUP_SUCCESS, SET_SUBHEAD_DETAIL, SET_USER_PROFILE} from "../constants/constants";
import { InitialState } from "../store/initialState";


export function productReducer(state = InitialState, action) {
    switch (action.type) {
        case SET_PRODUCT_LIST:
            return{
                ...state,
                productList:action.payload
            }
            case SET_PRODUCT_DETAILS:
            return{
                ...state,
                productDetails:action.payload
            }
            case SET_PRODUCT_FEATURES:
                return{
                    ...state,
                    productFeatures:action.payload
                }
                case SET_LOGIN_DETAIL:
                return{
                    ...state,
                    loginDetail:action.payload
                }
                case SET_LOGIN_SUCCESS:
                return{
                    ...state,
                    isLogin:true
                }
                case SET_SIGNUP_DETAIL:
                    return{
                        ...state,
                        signupDetail:action.payload
                    }
                    case SET_SIGNUP_SUCCESS:
                return{
                    ...state,
                    isSignup:true
                }
                case SET_USER_PROFILE:
                    return{
                        ...state,
                       userProfile:action.payload
                    }
                    case SET_SUBHEAD_DETAIL:
                        return{
                            ...state,
                           subHead:action.payload
                        }
                        case SET_CART_DETAIL:
                            return{
                                ...state,
                                cartDetail:action.payload
                            }
    
        default:
            return{
                ...state
            }
            break;
    }

}