import { SUB_HEADER_TEXT } from "../../Constants/constants";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import photo from "../../assets/phone.png";
import "./SubHeader.css";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import { BsFillPersonFill } from "react-icons/bs";
import { BsFillLockFill } from "react-icons/bs";
import { useNavigate } from "react-router-dom";
import { useSelector, useDispatch, connect } from "react-redux/es/exports";
import {
  setLoginSuccess,
  setSignupSuccess,
  setSubHeadDetail,
} from "../../Redux/action/action";
import { useEffect, useState } from "react";

function SubHeader(props) {
  const navigate = useNavigate();

  const isLogin = useSelector((state) => state.productFeature.isLogin);
  const isSignup = useSelector((state) => state.productFeature.isSignup);
  const dispatch = useDispatch();

  useEffect(() => {
    let isLoggedIn = localStorage.getItem("isLoggedIn");
    if (isLoggedIn == "true") {
      dispatch(setLoginSuccess(true));
    }
  }, []);

  useEffect(() => {
    let user = localStorage.getItem("user");
    user = JSON.parse(user);
    props.setSubHeadDetail(user);
    let isSignedUp = localStorage.getItem("isSignedUp");
    if (isSignedUp == "true") {
      dispatch(setSignupSuccess(true));
    }
  }, []);

  const onLogout = () => {
    localStorage.removeItem("isLoggedIn");
    localStorage.removeItem("isSignedUp");

    // Call logout API
    window.location.reload();
  };
  const profileDetail = () => {
    navigate("/Profile");
  };

  return (
    <Container fluid>
      <Row className="sub-header-container">
        <Col md={9}>
          <img src={photo}></img>
          {SUB_HEADER_TEXT}
        </Col>
        <Col md={3}>
          

          {isSignup || isLogin ? (
            <>
              <Button className="btn2" variant="link" onClick={profileDetail}>
                <BsFillPersonFill />
                {props.subHead && <span>{props.subHead.name}</span>}
              </Button>
              <Button onClick={onLogout} className="btn2" variant="link">
                <BsFillLockFill /> Logout
              </Button>
            </>
          ) : (
            <>
              <Button
                onClick={() => {
                  navigate("/login");
                }}
                className="btn1"
                variant="link"
              >
                <BsFillLockFill /> Log In
              </Button>
              <Button
                onClick={() => {
                  navigate("/SignUp");
                }}
                className="btn2"
                variant="link"
              >
                <BsFillPersonFill />
                Sign Up
              </Button>
            </>
          )}
        </Col>
      </Row>
    </Container>
  );
}
const mapStateToProps = (state) => {
  return {
    subHead: state.productFeature.subHead,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    setSubHeadDetail: (payload) => dispatch(setSubHeadDetail(payload)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(SubHeader);
