import BridgestoneImg from "../../../common/Components/atom/BridgestoneItem/BridgestoneImg";
import { useEffect, useState } from "react";
import { useParams } from 'react-router-dom';
import BridgestoneHeadDetail from "./BridgestoneHeadDetail";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Review from "./Review";

export default function BridgestoneDetails(){
const[tyreDetail,setTyreDetail]=useState(null);
const[head,setHead]=useState('');
const[data,setData]=useState('');
const { id } = useParams();

useEffect(()=>{
    fetch("http://localhost:3000/brandBridgestone/"+id)
    .then(res=>res.json()).then((res)=>{
      setTyreDetail(res)
    })
      },[])

      useEffect(()=>{
        fetch("http://localhost:3000/brandBridgestone/"+id)
        .then(res=>res.json()).then((res)=>{
          setHead(res)
        })
          },[])
            useEffect(()=>{
        fetch("http://localhost:3000/brandBridgestone/"+id)
        .then(res=>res.json()).then((res)=>{
          setData(res)
        })
          },[])

    return(
        <>
        <Container>
            <Row>
                {
 <BridgestoneHeadDetail brandBridgestone={head}></BridgestoneHeadDetail>
                }
            </Row>
       
            <Row>
                {
                    tyreDetail &&
                    <BridgestoneImg brandBridgestone={tyreDetail}></BridgestoneImg>
                }
            </Row>
            <Row>
                {
                    data &&
                    <Review brandBridgestone={data}></Review>
                }
            </Row>
        </Container>
        </>
    )
}