import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './FeaturesInfo.css';
export default function FeaturesInfo(props) {

    return (
        <>
            <Row>
                {
                    props.informationList.map((item) => {
                        return <Col xs={12} md={4} lg={4}>
                            <div className="feature-info">
                            <span className="feature-title">{item.title}</span>
                          
                        <div className="featureText">{item.text}</div>

                            </div>
                        
                    </Col>
                        
                    })


                }


            </Row>
        </>
    )
}