import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Table from 'react-bootstrap/Table';
import './Cart.css';
import CartTable from '../../../common/Components/atom/CartTable/CartTable';
import ApolloItem from '../../../common/Components/atom/ApolloItem/ApolloItem';
import { useEffect, useState } from "react";
import { useParams } from 'react-router-dom';
import { setCartDetail } from '../../../Redux/action/action';
import { connect } from 'react-redux';


export default function Cart(props){
   const{id}=useParams();
    const [cart,setCart]=useState([]);

    useEffect(()=>{
   var cartData=localStorage.getItem("cartData");
   if(cartData){
    cartData=JSON.parse(cartData);
    setCart(cartData)
   }
    },[])




    return(
        <>
       <Container>
        <Row>
        <Col md={12} lg={12} xs={12}>
                <Table responsive="sm">
                    <thead className='table-head'>
                        <tr>
                            <th style={{ width: "20%", borderRadius: "23px 0 0 0" }}>CART ITEM</th>
                            <th style={{ width: '14%' }}>QUANTITY</th>
                            <th style={{ width: '10%' }}>ITEM PRICE</th>
                            <th style={{ width: '15%' }}>DISCOUNT</th>
                            <th style={{ width: '10%', borderRadius: "0 23px 0 0" }}>TOTAL</th>
                        
                        </tr>
                    </thead>
                    <tbody>
                               {
                                cart && cart.map((item)=>{
                                    return <div>{item.title}</div>
                                })
                               }
                    </tbody>
                </Table>
            </Col>
        
          
        </Row>
       </Container>
        </>
    )
}
