import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useEffect } from "react";
import { useState } from "react";
// import './Bridgestone.css';

import BridgestoneItem from '../../../common/Components/atom/BridgestoneItem/BridgestoneItem';

export default function Bridgestone(){
    const[productList,setProductList]=useState([]);

    useEffect(()=>{
  fetch("http://localhost:3000/brandBridgestone").then(res=>res.json()).then((res)=>{
    setProductList(res)
  })
    },[])

    return(

        <>
       <Container>
      
        <Row>
            <Col md={12} xs={12} lg={12} className="product-page-container">
                <h6>
                BRIDGESTONE Car Type Prices
                </h6>
            </Col>
        </Row>
        <Row>
            {
             productList && productList.map((item)=>{
                return  <BridgestoneItem brandBridgestone={item}></BridgestoneItem>
             })
            
            }
        </Row>
       </Container>
        </>
    )
}