import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

export default function ApolloHeadDetail(props){

    return(
        <>
        <Container>
        <Row>
            <Col md={12} xs={12} lg={12} className="product-page-container">
                <h6>
                    {
                        props.apollo.head
                    }
                </h6>
            </Col>
            </Row>

        </Container>
        
        </>
    )
}