
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Table from 'react-bootstrap/Table';
import Col from 'react-bootstrap/Col';
import { useEffect, useState } from "react";
import {useParams} from 'react-router-dom';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import Button from 'react-bootstrap/Button';

export default function ApolloReview(props){
    
    return(
        <>
      
        <Container className="overreview-container">
        <h5>QUICK OVERVIEW</h5>
        <h6>{props.apollo.features.heading}</h6>
        <div className="review-table">
        <Row >
        <Col className="review-row-1" xs={4} md={4} >
          width
        </Col>
        <Col className="review-row-1" xs={1} md={1}>
         {props.apollo.features.width}
        </Col>
        <Col className="review-row-1"xs={3} md={3}>
        Aspect Ratio
        </Col>
        <Col className="review-row-1"xs={4} md={4}>
        {props.apollo.features.asRatio}
        </Col>
      </Row>
      <Row >
        <Col className="review-row-1" xs={4} md={4} >
        Rim diameter in inches
        </Col>
        <Col className="review-row-1" xs={1} md={1}>
        {props.apollo.features.rim}
        </Col>
        <Col className="review-row-1"xs={3} md={3}>
        Load Index
        </Col>
        <Col className="review-row-1"xs={4} md={4}>
        {props.apollo.features.loadIndex}
        </Col>
      </Row>
      <Row >
        <Col className="review-row-1" xs={4} md={4} >
        Compatible Cars
        </Col>
        <Col className="review-row-1" xs={1} md={1}>

        </Col>
        <Col className="review-row-1"xs={3} md={3}>
        Warranty Clause
        </Col>
        <Col className="review-row-1"xs={4} md={4}>
          
         </Col>
      </Row>
      <Row >
        <Col className="review-row-1" xs={4} md={4} >
        Speed Rating
        </Col>
        <Col className="review-row-1" xs={1} md={1}>
        {props.apollo.features.speedRating}
        </Col>
        <Col className="review-row-1"xs={3} md={3}>
        Tubeless
        </Col>
        <Col className="review-row-1"xs={4} md={4}>
        {props.apollo.features.tubeless}
        </Col>
      </Row>
        </div>
     
         </Container>
    
           
        
        
        </>
    )
}