import HeaderTitle from "../Components/atom/HeaderTitle/HeaderTitle";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './Footer.css';
import ImportantLinks from "../Components/atom/ImportantLinks/ImportantLinks";
import Support from "../Components/atom/Support/Support";
import { Link } from 'react-router-dom';



export default function Footer() {
    var carBrands = [
        {
            name: "BMW",
            url: "#/ProductPage"
        },
        {
            name: "Tata",
            url: "#/ProductPage"
        },
        {
            name: "Honda",
            url: "/ProductPage"
        },
        {
            name: "Toyota",
            url: "#/ProductPage"
        },
        {
            name: "Maruti",
            url: "#/ProductPage"
        },
        {
            name: "Chevrolet",
            url: "/ProductPage"
        },
        {
            name: "KIA",
            url: "/ProductPage"
        },
        {
            name: "Ford",
            url: "#/ProductPage"
        },
        {
            name: "Hyundai",
            url: "#/ProductPage"
        },
        {
            name: "Datsun",
            url: "#/ProductPage"
        },
        {
            name: "Fiat",
            url: "#/ProductPage"
        }
    ];
    var tyreBrand = [
        {
            name: "Bridgestone",
            url: "#/Bridgestone"
        },
        { name: "Yokohama", url: "#/Yokohama" },
        { name: "Michelin", url: "#/Bridgestone" },
        { name: "Continental", url: "#/Bridgestone" },
        { name: "Apollo", url: "#/Apollo" },
        { name: "Ceat", url: "#/Ceat" },
        { name: "Goodyear", url: "#/Bridgestone" }

    ];
    var impLinks = [
        {
            name: "About Us",
            url: "#/SitesAbout"
        },
        {
            name: "Contact Us",
            url: "#/ContactDetails"
        },
        {
            name: "Privacy policy",
            url: "#/SitesAbout"
        },
        {
            name: "Terms & Conditions",
            url: "/SitesAbout"
        },
        {
            name: "Order Refunds",
            url: "/SitesAbout"
        },
        {
            name: "Our Blogs",
            url: "/SitesAbout"
        }
    ];
    var support = [
        { name: "+91 999-982-3205" },
        { name: "Support@tyrewaale.com" }
    ]
    return (
        <>
            <footer className="footer">
                <Container className="footer-container">
                    <Row>
                        <Col lg={3} md={3} xs={6}>
                            <HeaderTitle title={"CAR BRANDS"}></HeaderTitle>
                            <Row>
                                <Col md={6} className="carBrand-footer-link-1">
                                    {
                                        carBrands.splice(0, 6).map((item, index) => {
                                            return <li key={"col1_" + index}><a href={item.url}>{item.name}</a></li>
                                        })
                                    }
                                </Col>
                                <Col md={6} className="carBrand-footer-link-2">

                                    {
                                        carBrands.map((item, index) => {
                                            return <li key={"col2_" + index}><a href={item.url}>{item.name}</a></li>
                                        })
                                    }
                                </Col>
                            </Row>

                        </Col>
                        <Col md={3} xs={6}>
                            <HeaderTitle title={"TYRE BRANDS"}></HeaderTitle>
                            <Row>
                                <Col md={6} className="tyreBrand-footer-link-1">
                                    {
                                        tyreBrand.splice(0, 4).map((item, index) => {
                                            return <li key={"col1_" + index}><a href={item.url}>{item.name}</a></li>
                                        })
                                    }
                                </Col>
                                <Col md={6} className="tyreBrand-footer-link-2">
                                    {
                                        tyreBrand.map((item, index) => {
                                            return <li key={"col2_" + index}><a href={item.url}>{item.name}</a></li>
                                        })
                                    }
                                </Col>
                            </Row>
                        </Col>
                        <Col md={3} xs={6}>
                            <HeaderTitle title={"IMPORTANT LINKS"}></HeaderTitle>
                            <ImportantLinks impLinks={impLinks}></ImportantLinks>
                        </Col>
                        <Col md={3} xs={6}>
                            <HeaderTitle title={"SUPPORT"}></HeaderTitle>
                            <Support support={support}></Support>
                            <HeaderTitle title={" FOLLOW US"}></HeaderTitle>
                        </Col>
                    </Row>
                </Container>
            </footer>

        </>

    )
}