import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import './BridgestoneItem.css';
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import { useCart } from "react-use-cart";

export default function BridgestoneItem(props) {
    const { addItem } = useCart();

    const addToCart=()=>{
        addItem(props.brandBridgestone);
    }

    return (
        <> 
          
                <Col md={3} lg={3} xs={12} className='product-container'>

                    <div className='product-thumb'>
                        <Link to={"/Bridgestone/" + props.brandBridgestone.id}>
                            <img className="product-image" src={props.brandBridgestone.images}></img>
                        </Link>

                        <div className='product-info'>
                            <div>
                                <img className="product-logo"src={props.brandBridgestone.images1}></img>
                            </div>
                            <div className='product-title'>
                                {props.brandBridgestone.title}
                            </div>
                            <div className='product-description'>
                                {props.brandBridgestone.description}
                            </div>
                            <div className='product-price'>
                                $ {props.brandBridgestone.price}
                            </div>
                        </div>

                        <Button onClick={addToCart}style={{width:"100%",background:"#0d4e9d"}}>Add to Cart</Button>
                    </div>



                </Col>
           

        </>
    )
}