import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import './ProductItem.css';
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
export default function ProductItem(props){
    return(
        <>
       
       
           
       
     
        <Col md={3} lg={3} xs={12} className='product-container'>
            {/* <Link style={{textDecoration:"none"}} to={"/ProductPage/"+props.products.id}> */}
            <div className='product-thumb'>
                <Link to={"/ProductPage/"+props.products.id}>
                 <img className="product-image" src={props.products.images}></img>
                </Link>
           
            <div className='product-info'>
            <div className='product-title'>
                {props.products.title}
            </div>
            <div className='product-description'>
                {props.products.description}
            </div>
            <div className='product-price'>
               $ {props.products.price}
            </div>
            </div>
            <Button onClick={props.addToCart} style={{width:"100%",background:"#0d4e9d"}}>Add to Cart</Button>
            
        </div>
           
        

        </Col>
       
        
       
        
        </>
    )
}