import Container from 'react-bootstrap/Container';
import './CartPage.css';
import { useCart } from 'react-use-cart';
import { Row } from 'react-bootstrap';
import Table from 'react-bootstrap/Table';
import { BsCurrencyRupee ,BsTrash3} from "react-icons/bs";
import {Button} from 'react-bootstrap';

export default function CartPage() {
    const {
        isEmpty,
        totalUniqueItems,
        items,
        updateItemQuantity,
        removeItem,
        cartTotal
      } = useCart();

  return (
    <Container style={{width:"100%"}}>
      <h4 className="shoppingcart-title">
        {
           isEmpty?"The Empty Shopping Cart":"Your Shopping Cart"
        }
      </h4>
      <Row className='cart-row'>
      <Table responsive="sm" >
                    <thead className='table-head'>
                        <tr>
                            <th style={{ width: "20%", borderRadius: "23px 0 0 0" }}>CART ITEM</th>
                            <th style={{ width: '14%' }}>QUANTITY</th>
                            <th style={{ width: '10%' }}>ITEM PRICE</th>
                            <th style={{ width: '15%' }}>DISCOUNT</th>
                            <th style={{ width: '10%', borderRadius: "0 23px 0 0" }}>TOTAL</th>
                        
                        </tr>
                    </thead>
                    <tbody>

                        {
                            items.map((item,index)=>{
                                return (
                                    <>
                                    <tr key={index}>
                                    <td style={{display:"flex"}}>
                                        <img style={{width:"40%",alignItems:"left"}}src={item.images}></img>
                                        <div>
                                        <img style={{width:"50%",alignItems:"center"}}src={item.images1}></img>
                                            <p style={{marginTop:"20px"}}>
                                                {item.title}   <br></br> {item.description}
                                            </p>
                                           
                                        </div>
                                    </td>
                                    <td>
                                        <span><Button className="btn-minus" onClick={() => updateItemQuantity(item.id, item.quantity - 1)}>-</Button></span>
                                    <span className="input-qty">{item.quantity}</span>
                                    <span><Button className="btn-plus" onClick={() => updateItemQuantity(item.id, item.quantity + 1)}>+</Button></span>
                                    </td>
                                    <td>
                                        <BsCurrencyRupee/>{item.price}
                                    </td>
                                    <td></td>
                                    <td> <BsCurrencyRupee/>{item.price}</td>
                                     <button  onClick={() => removeItem(item.id)}><BsTrash3/></button>
                                    
                                    </tr>
                                  
                                     
                                        <tr>
                                        <td colSpan={3} style={{borderBottom:"hidden",borderLeft:"hidden"}}></td>
                                        <td>
                                            <b>Grand Total</b>
                                        </td>
                                        <td>
                                            {cartTotal}
                                        </td>
                                        </tr>
                                       
                                    
                                    </>
                                )
                                        
                            })
                        }
                       
                                    

                                
                           
                    </tbody>
                    </Table>

      </Row>
    </Container>
  );
}
