import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useEffect } from "react";
import { useState } from "react";
import ApolloItem from '../../../common/Components/atom/ApolloItem/ApolloItem';

export default function Apollo(){
    const[productList,setProductList]=useState([]);

    useEffect(()=>{
  fetch("http://localhost:3000/apollo")
  .then(res=>res.json())
  .then((res)=>{
    setProductList(res)
  })
    },[])
    return(
        <>
        <Container>
            
            <Row>
            <Col md={12} xs={12} lg={12} className="product-page-container">
                <h6>
                APOLLO Car Tyre Prices
                </h6>
            </Col>
            </Row>
            <Row>
            {
             productList && productList.map((item)=>{
                return  <ApolloItem apollo={item}></ApolloItem>
             })
            
            }
        </Row>
        </Container>
        
        
        </>
    )
}