import { useEffect, useState } from "react";
import { useParams } from 'react-router-dom';
import ProductItem from "../../../common/Components/atom/ProductItem/ProductItem";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './ProductDetail.css';
import OverReview from "./OverReview";
import { connect } from "react-redux/es/exports";
import { setProductDetails, setProductFeatures } from "../../../Redux/action/action";
import HeadDetails from "./HeadDetails";



 function ProductDetail(props) {
    const[data,setData]=useState('');
    

    const { id } = useParams();


    useEffect(() => {

        fetch("http://localhost:3000/products/" + id)
            .then(res => res.json())
            .then((res) => {
               props.setProductDetails(res)
            })
    }, []);

    useEffect(() => {

        fetch("http://localhost:3000/products/" + id)
            .then(res => res.json())
            .then((res) => {
                props.setProductFeatures(res)
            })
    }, []);

    useEffect(() => {

        fetch("http://localhost:3000/products/" + id)
            .then(res => res.json())
            .then((res) => {
                setData(res)
            })
    }, []);


    return (
        <>
            <Container>
            <Row>
                {
                    <HeadDetails products={data}></HeadDetails>
                }
            </Row>
        
                <Row>
                    {
                       props.productDetails && 
                        <ProductItem products={props.productDetails}></ProductItem>
                    }
                </Row>
                <hr ></hr>
                <Row>
                    {
                       props.productFeatures &&
                        <OverReview products={props.productFeatures}></OverReview>
                    }

                </Row>





            </Container>
        </>
    )
}
const mapStateToProps=(state)=>{
    return{
 productDetails:state.productFeature.productDetails,
 productFeatures:state.productFeature.productFeatures
    }
}
const mapDispatchToProps=(dispatch)=>{
    return{
   setProductDetails:(payload)=>dispatch(setProductDetails(payload)),
   setProductFeatures:(payload)=>dispatch(setProductFeatures(payload))
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(ProductDetail);