import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


export default function ImportantLinks(props){
    return(
        <>
        <Row>
                    <Col md={12} className="impLink-footer-link">
                    {
                          props.impLinks.map((item, index) => {
                                return <li key={"col3_" + index}>
                                   <a href={item.url}>{item.name}</a>
                                    </li>
                            })
                        }
                    </Col>
                </Row>
        </>
    )
}
