import React, { useState } from 'react';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './Search.css';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import tyreSize from "../../../../assets/tyresize.png";
import carBrand from '../../../../assets/carBrand.png';
import {useNavigate } from "react-router-dom";

export default function Search() {
  const [key, setKey] = useState('home');
  const[brand,setBrand]=useState('');
const navigate=useNavigate();

const handleChange=(e)=>{
  setBrand(e.target.value)
}

  var brandName=[
    {name:"Bridgestone"},
    {name:"Yokohama"},
    {name:"Apollo"},
    {name:"CEAT"}
  ]

  const searchTyre=(e)=>{
    if(brand=="Bridgestone"){
      navigate("/Bridgestone")
    }else if(brand=="Yokohama"){
      navigate("/Yokohama")
    }else if(brand=="Apollo"){
      navigate("/Apollo")
    }else if(brand=="CEAT"){
      navigate("/Ceat")
    }
  }
  return (
    <>
      <Container className="tabs-container">
        <Row>
          <Col md={12} xs={12}>
          <div className='tabs-div'>
          <Tabs
            id="controlled-tab-example"
            defaultActiveKey="0"
            onSelect={(k) => setKey(k)}
            className="mb-3"
          >
          
            <Tab eventKey="0" title="TYRE SIZE">
              <div className='form-container'>
                <div className="tabs-form">
                  <Form.Select size="sm" value={brand} onChange={handleChange} >
                    <option>Search Brand</option>
                    {
                     brandName.map((item,index)=>{
                      return <option value={item.name} key={"tab_1"+index}>{item.name}</option>
                     })
                    }
                  </Form.Select>
                  <Form.Select size="sm">
                    <option>Profile</option>
                  </Form.Select>
                  <Form.Select size="sm">
                    <option>Rim</option>
                  </Form.Select>

                </div>
                <div>
                  <img src={tyreSize} style={{ width: '100%' }}></img>
                </div>

                <div className="d-grid gap-2">
                  <Button onClick={searchTyre}variant="primary" size="lg" className='tabs-btn'>
                    Search Tyres
                  </Button>
                </div>
              </div>
            </Tab>
           

           
            <Tab eventKey="1" title="CAR BRANDS">
              <div className='form-container'>
                <div className="tabs-form-1">
                  <Form.Select size="sm" id='form-select'>
                    <option>Select Manufaturer</option>
                  </Form.Select>
                  <Form.Select size="sm" id='form-select'>
                    <option>Select Vehical Model</option>
                  </Form.Select>
                 

                </div>
                <div>
                  <img src={carBrand}
                   style={{ width: '50%',height:'50%',marginLeft:'170px',marginBottom:'30px',marginTop:'20px' }}></img>
                </div>

                <div className="d-grid gap-2">
                  <Button variant="primary" size="lg" className='tabs-btn'>
                    Search Tyres
                  </Button>
                </div>
              </div>
            </Tab>

          </Tabs>
        </div>

          </Col>
        </Row>
        

      </Container>

    </>
  )
}