import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Link } from 'react-router-dom';
import './Login.css';
import { setLoginDetail, setLoginSuccess } from '../../../Redux/action/action';
import { connect } from "react-redux/es/exports";
import { useNavigate } from "react-router-dom";

 function Login(props) {
const navigate=useNavigate();
    const setLoginEmail=(e)=>{
        let loginDetail=props.loginDetail;
        loginDetail.email=e.target.value
        setLoginDetail(loginDetail);
    }
    const setLoginPassword=(e)=>{
        let loginDetail=props.loginDetail;
        loginDetail.password=e.target.value
        setLoginDetail(loginDetail);
    }
  
    const loginForm=()=>{
        var loginID={
            ...props.loginDetail,
            id:1
        }
   fetch("http://localhost:3000/logins",{
    method:"POST",
    body: JSON.stringify(loginID)
   }).then(res=>res.json()).then((res)=>{
    console.log(res);
    props.setLoginSuccess(true);
    localStorage.setItem("isLoggedIn",true);
    // localStorage.setItem("user",JSON.stringify(res))
    localStorage.setItem("user",JSON.stringify(props.loginDetail))
     navigate("/")
   })

    }



    return (
        <>
            <div className="login-container">
                <Container className='sub-container'>
                    <Row className="login-form">
                        <Col xs={12} md={12} >
                            <span style={{ fontWeight: '400', fontSize: '28px', marginTop: '0%' }}>Sign-In</span>
                            <Form onSubmit={(r)=>{
                                r.preventDefault()
                            }}>
                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <Form.Label>Email or Phone Number</Form.Label>
                                    <Form.Control onChange={(e)=>{
                                        setLoginEmail(e)
                                    }} value={props.loginDetail.email}type="email" placeholder="Email/phone" />
                                </Form.Group>

                                <Form.Group className="mb-3" controlId="formBasicPassword">
                                    <Form.Label>Password</Form.Label>
                                    <Form.Control onChange={(e)=>{
                                        setLoginPassword(e)
                                    }} value={props.loginDetail.password}  type="password" placeholder="Password" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="formBasicCheckbox">
                                    <Row>
                                        <Col md={6} xs={6}>
                                            <Form.Check type="checkbox" label="Remember me" />
                                        </Col>
                                        <Col md={6} xs={6}>
                                            <Link style={{ textDecoration: "none", fontSize: "12px" }} to={''}>forgot password</Link>
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Button onClick={loginForm} className="btn-login" variant="primary" type="submit">
                                    Log In
                                </Button><hr style={{margingTop:"100px",color:"black"}}></hr>
                                <div className='tbs-divider'>
                                <p>Don't have an account ?
                                <a href="/SignUp" style={{textDecoration:"none"}} >Sign Up</a>
                                </p>
                                
                                </div>

                            </Form>
                        </Col>

                    </Row>
                </Container>
            </div>

        </>
    )
}
const mapStateToProps=(state)=>{
    return{
 loginDetail:state.productFeature.loginDetail,
 
    }
}
const mapDispatchToProps=(dispatch)=>{
    return{
  setLoginDetail:(payload)=>dispatch(setLoginDetail(payload)),
  setLoginSuccess:(payload)=>dispatch(setLoginSuccess(payload))
   
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(Login);