import { useEffect,useState } from "react";
import { Container, Row } from "react-bootstrap";
import { useParams } from 'react-router-dom';
import YokohamaImg from "../../../common/Components/atom/YokohamaItem/YokohamaImg";
import YokoReview from "./YokoReview";
import YokoDetailHead from "./YokoDetailHead";

export default function YokohamaDetail(){
const[tyreDetail,setTyreDetail]=useState(null);
const[head,setHead]=useState('');
const[feature,setFeature]=useState('');
const { id } = useParams();

useEffect(()=>{
 fetch("http://localhost:3000/brandYoko/" + id)
 .then(res=>res.json())
 .then((res)=>{
    setTyreDetail(res)
 })
},[])
useEffect(()=>{
 fetch("http://localhost:3000/brandYoko/" + id)
 .then(res=>res.json())
 .then((res)=>{
    setHead(res)
 })
},[])
useEffect(()=>{
    fetch("http://localhost:3000/brandYoko/" + id)
    .then(res=>res.json())
    .then((res)=>{
       setFeature(res)
    })
   },[])
    return(
        <>
        <Container>
            <Row>
                {
                    head &&
                    <YokoDetailHead brandYoko={head}></YokoDetailHead>
                }
            </Row>
            <Row>
                {
                    tyreDetail &&
                    <YokohamaImg brandYoko={tyreDetail}></YokohamaImg>
                }
            </Row>
            <Row>
                {
                    feature &&
                    <YokoReview brandYoko={feature}></YokoReview>
                }
            </Row>
        </Container>
        </>
    )
}