import { useEffect, useState } from "react";
import { useParams } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import CeatImg from "../../../common/Components/atom/CeatItem/CeatImg";
import CeatHeadDetail from "./CeatHeadDetail";
import CeatReview from "./CeatReview";


export default function CeatDetails() {
    const [tyreDetail, setTyreDetail] = useState(null);
    const [head, setHead] = useState('');
    const [data, setData] = useState('');
    const { id } = useParams();

    useEffect(() => {
        fetch("http://localhost:3000/brandCeat/" + id)
            .then(res => res.json()).then((res) => {
                setTyreDetail(res)
            })
    }, [])

    useEffect(() => {
        fetch("http://localhost:3000/brandCeat/" + id)
            .then(res => res.json()).then((res) => {
                setHead(res)
            })
    }, [])
    useEffect(() => {
        fetch("http://localhost:3000/brandCeat/" + id)
            .then(res => res.json()).then((res) => {
                setData(res)
            })
    }, [])

    return (
        <>
            <Container>
                <Row>
                    {
                        <CeatHeadDetail brandCeat={head}></CeatHeadDetail>
                    }
                </Row>

                <Row>
                    {
                        tyreDetail &&
                        <CeatImg brandCeat={tyreDetail}></CeatImg>
                    }
                </Row>
                <Row>
                    {
                        data &&
                        <CeatReview brandCeat={data}></CeatReview>
                    }
                </Row>
            </Container>
        </>
    )
}