import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Button from 'react-bootstrap/Button';
import { Link } from 'react-router-dom';
import { useCart } from "react-use-cart";

export default function CeatItem(props) {
    const { addItem } = useCart();

    const addToCart=()=>{
        addItem(props.brandCeat);
    }
    return (
        <>
          
                <Col md={3} lg={3} xs={12} className='product-container'>

                    <div className='product-thumb'>
                        <Link to={"/Ceat/" + props.brandCeat.id}>
                            <img className="product-image" src={props.brandCeat.images}></img>
                        </Link>

                        <div className='product-info'>
                            <div>
                                <img className="product-logo"src={props.brandCeat.images1}></img>
                            </div>
                            <div className='product-title'>
                                {props.brandCeat.title}
                            </div>
                            <div className='product-description'>
                                {props.brandCeat.description}
                            </div>
                            <div className='product-price'>
                                $ {props.brandCeat.price}
                            </div>
                        </div>
<Button onClick={addToCart} style={{width:"100%",background:"#0d4e9d"}}>Add to Cart</Button>

                    </div>



                </Col>
           

        </>
    )
}