import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './MarqueeLine.css';
import wheel from '../../../../assets/wheels.png';
import car from '../../../../assets/car.png';
import Container from 'react-bootstrap/Container';


export default function MarqueeLine(){

    return(
        <>
        <Container fluid>
        <Row className="marquee-line-row">
            <Col md={12} xs={12} className='marquee-line'>
            <marquee  width="100%" direction="left"  >
            <span className="ticket-wrap-1">
                <img src={wheel}></img>
                We provide complete Tyre Solutions For All Luxury cars Like BMW.
            </span>   
             <span className="ticket-wrap-2">
             <img src={car}></img>
             One stop place to buy car tyres online
            </span>            
        </marquee>
            </Col>
           
        </Row>
        </Container>
        
        
        
        </>
    )
}