import Breadcrumb from 'react-bootstrap/Breadcrumb';
import { BsChevronRight } from "react-icons/bs";
import { Link } from "react-router-dom";
import './BreadcrumbsItem.css';

export default function BreadcrumbsItem(props) {

    return (
        <>
            <Breadcrumb className='Breadcrumbs-item'>
                {
                    props.BreadcrumbsItem.map((item) => {
                        return <div>

                            <Link style={{textDecoration:"none",color:"white"}} to={'/'}>{item.page}</Link>  <BsChevronRight />
                            <Link style={{textDecoration:"none",color:"white"}} to={'/ProductPage'}>{item.page1}</Link>
                            <span>
                                {item.name}
                            </span>
                        </div>
                    })
                }
            </Breadcrumb>
        </>

    )
}