import BreadcrumbsItem from '../../common/Components/atom/Breadcrumbs/BreadcrumbsItem';
import './SiteAboutBc.css';
export default function SiteAboutBc(){
    var BreadcrumbsData = [
        {
            page: "Home"
        },
        
        {
            name: "About"
        }
    ]
    return(
        <>
         <div className='breadcrumbs-container'>
        <BreadcrumbsItem BreadcrumbsItem={BreadcrumbsData}></BreadcrumbsItem>
        </div>
        </>
    )
}