import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import logo from '../../assets/logo1.png';
import Button from 'react-bootstrap/Button';
import { BsFillCartFill } from "react-icons/bs";
import { useNavigate } from "react-router-dom";
import Navbar from 'react-bootstrap/Navbar';

import './Header.css';
import { useCart } from "react-use-cart";




export default function Header() {
    const navigate=useNavigate();
    const { totalItems } = useCart();

    return (
<Navbar collapseOnSelect expand="lg" className="navbar-style nav-default stick-top">
<Container>
<Navbar.Brand href=""><img src={logo}></img></Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="homebtn">
            <Button onClick={()=>{
        navigate("/")
    }} variant="primary">Home</Button>
            </Nav>
          <Nav className="me-auto">
            
            <NavDropdown title="Tyres Brand" id="collasible-nav-dropdown">
            <NavDropdown.Item href="#/ProductPage">BMW</NavDropdown.Item>
            <NavDropdown.Item href="#/Bridgestone">Bridgestone</NavDropdown.Item>
              <NavDropdown.Item href="#/Yokohama">Yokohama</NavDropdown.Item>
              <NavDropdown.Item href="#/Ceat">CEAT</NavDropdown.Item>
              <NavDropdown.Item href="#/Apollo">Apollo</NavDropdown.Item>
            </NavDropdown>
            <NavDropdown title="Cars Brand" id="collasible-nav-dropdown">
              <NavDropdown.Item href="#/Bridgestone">Bridgestone</NavDropdown.Item>
              <NavDropdown.Item href="#/Yokohama">Yokohama</NavDropdown.Item>
              <NavDropdown.Item href="#/Ceat">CEAT</NavDropdown.Item>
              <NavDropdown.Item href="#/Apollo">Apollo</NavDropdown.Item>
            </NavDropdown>
            <Nav className="cartbtn">
            <Button onClick={()=>{
        navigate("/CartPage")
    }} variant="primary"> <BsFillCartFill style={{color:"gray"}}/>({totalItems })Cart</Button>
            </Nav>
            
            <Nav.Link eventKey={2}>

            <Form.Control
                    type="password"
                    id="inputPassword5"
                    placeholder='Search'
                />
            </Nav.Link>
          </Nav>
          </Navbar.Collapse>
</Container>
        
        </Navbar>
    )
}
