import './KnowMore.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import pointer from '../../../../assets/hand.jpg';

export default function KnowMore(){

    return(
        <>
        <Container fluid>
            <Row className="know-more-row">
                <Col xs={12} md={7} lg={7} className="get-tyres-col">
                    <div className='get-tyres'>
                        <p>How to Get Tyres fitted</p>
                        <p>at your doorstep !</p>
                    </div>
                </Col>
                <Col xs={12} md={4} lg={4} className="get-tyres-col-2">
                <Button variant="light" className='know-more-btn'>
                    <img src={pointer} style={{width:'20px',height:'20px',paddingRight:'5px'}}></img>
                    Know More...</Button>
                </Col>
            </Row>

        </Container>

        </>
    )
}