import { Col, Container, Row, Table } from "react-bootstrap";
import './YokohamaImg.css';
import { BsFillStarFill } from "react-icons/bs";
import Button from 'react-bootstrap/Button';
import { BsFillCartFill } from "react-icons/bs";
import { useCart } from "react-use-cart";

export default function YokohamaImg(props){
  const { addItem } = useCart();

  const addToCart=()=>{
      addItem(props.brandYoko);
  }

    return(
        <>
        <Container style={{marginTop:"20px"}}>
            <Row>
            <Col md={4} xs={4}>
          {
            <img style={{width:"80%",height:"auto"}} src={props.brandYoko.images}></img>
          }
        </Col>
        <Col md={4} xs={4}>
            {
                <>
                <img style={{width:"35%",height:"auto",marginLeft:"65px",padding:"5px"}}src={props.brandYoko.images1}></img>
                <h4>{props.brandYoko.title} {props.brandYoko.description}</h4>
                <hr></hr>
              
               <div style={{paddingBottom:"5px"}}>
                <span style={{fontSize:"20px"}}>M.R.P.:</span>
                <span style={{marginLeft:'150px',fontSize:"20px",fontWeight:"500"}}>Rs. &nbsp;{props.brandYoko.price}</span>
               </div>
               <div style={{paddingBottom:"5px"}}>
                <span style={{fontSize:"18px"}}>Price:</span>
                <span style={{marginLeft:'170px',fontSize:"15px",color:"red"}}>Rs. &nbsp;{props.brandYoko.price}</span>
               </div>
               <div style={{paddingBottom:"5px"}}>
                <span>Inclusive of all taxes</span>
               </div>
               <div style={{paddingBottom:"5px"}}>
                <span><BsFillStarFill/><BsFillStarFill/><BsFillStarFill/><BsFillStarFill/><BsFillStarFill/></span>
                <span style={{marginLeft:'110px',fontSize:"15px"}}>0 out of 5 Star	</span>
               </div>
                <div style={{paddingBottom:"5px"}}>
                <span style={{fontWeight:"500"}}>Stock Level:</span>
                <span style={{marginLeft:'150px',fontSize:"15px"}}>In Stock</span>
               </div>
               <div>
               <Button onClick={addToCart}className="cart_btn" variant="primary"><BsFillCartFill/>  Add to Cart</Button>
               </div>

                </>
                
            }
        </Col>
       
            <Col md={4} xs={4}>
          {
            <img style={{width:"100%",height:"auto"}} src={props.brandYoko.images2}></img>
          }
        </Col>
            </Row>
        </Container>
       
        
        </>
    )
}