import ProductItem from "../../../common/Components/atom/ProductItem/ProductItem";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useEffect } from "react";
import { useState } from "react";
import Breadcrumbs from "../../../common/Components/atom/Breadcrumbs/Breadcrumbs";
import './ProductPage.css';
import { setProductList } from "../../../Redux/action/action";
import { connect } from "react-redux/es/exports";


 function ProductPage(props){
 

    useEffect(()=>{
        
        fetch("http://localhost:3000/products")
        .then(res=>res.json())
           .then((res)=>{
               props.setProductList(res)
           })
        },[]);
        
   const addToCart=(item)=>{
    fetch('https://fakestoreapi.com/carts',{
            method:"POST",
            body:JSON.stringify(
                {
                    userId:5,
                    date:'2023-02-03',
                    products:[{productId:item.id,quantity:1}]
                }
            )
        })
            .then(res=>res.json())
            .then(json=>{
                // LocalStorage Database
                var carts= localStorage.getItem("cartData");
                if(!carts){
                    var cartData=[];
                    cartData.push(item);
                    localStorage.setItem("cartData",JSON.stringify(cartData))
                }else{
                    carts=JSON.parse(carts);
                    carts.push(item);
                    localStorage.setItem("cartData",JSON.stringify(carts))
                }
            })
   }
    return(
        <>
        <Breadcrumbs></Breadcrumbs>
       <Container>
        <Row>
            <Col md={12} xs={12} lg={12} className="product-page-container">
                <h6>
                BMW Car Type Prices
                </h6>
            </Col>
        </Row>
        <Row>
            {
                props.productList && props.productList.map((item)=>{
                    return <ProductItem products={item} addToCart={addToCart(item)}></ProductItem>
                })
            }
            
        </Row>
       </Container>
     
        </>
    )
}
const mapStateToProps=(state)=>{
    return{
 productList:state.productFeature.productList
    }
}
const mapDispatchToProps=(dispatch)=>{
    return{
   setProductList:(payload)=>dispatch(setProductList(payload))
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(ProductPage);