import "./profile.css";
import Container from "react-bootstrap/Container";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import ProfilePic from "../../../assets/default.png";
import {
  BsFillPersonFill,
  BsFillLockFill,
  BsListUl,
  BsFillHandbagFill,
  BsBoxArrowInRight,
} from "react-icons/bs";
import Nav from "react-bootstrap/Nav";
import Tab from "react-bootstrap/Tab";
import { connect } from "react-redux/es/exports";
import { setUserProfile } from "../../../Redux/action/action";
import { useEffect } from "react";
import Table from 'react-bootstrap/Table';
import { Button } from "react-bootstrap";
import Form from 'react-bootstrap/Form';


function Profile(props) {
    useEffect(()=>{
        let user=localStorage.getItem("user");
        user=JSON.parse(user);
        // fetch('http://localhost:3000/users/'+user.id)
        // .then(response=>response.json())
        // .then((res)=>{
        //     props.setUserProfile(res);
        // })
        props.setUserProfile(user)
    },[])


   
  return (
    <>
    {
        props.userProfile &&
        <>
 <Tab.Container id="left-tabs-example" defaultActiveKey="first">
        <Row>
           
                <Col md={3} xs={3} lg={3} className="profile-col-1">
            <div className="side-tabs">
              <div className="profile-pic">
                <img class="img-responsive" src={ProfilePic} alt="image" />
                <form>
                  <input type="hidden" name="_csrf" value="qmg1" />
                  <div class="form-group field-choose-file">
                    <input type="hidden" name="User[profile_file]" value="" />
                    <input
                      type="file"
                      id="choose-file"
                      class="choose-file"
                      name="User[profile_file]"
                      value=""
                    />
                    <label for="choose-file" class="upload-file">
                      <i class="fa fa-pencil"></i>
                    </label>
                  </div>
                </form>
              </div>
              <h4
                style={{
                  fontSize: "18px",
                  fontWeight: "500",
                  textAlign: "center",
                }}
              >
                {props.userProfile.name}
              </h4>
              <Nav variant="pills" className="flex-column">
                <Nav.Item>
                  <Nav.Link eventKey="first">
                    <BsFillPersonFill />
                    Profile
                  </Nav.Link>
                </Nav.Item>

                <Nav.Item>
                  <Nav.Link eventKey="second">
                    <BsFillLockFill />
                    Change Password
                  </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="third">
                    <BsListUl />
                    Update Profile
                  </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="fourth">
                    <BsFillHandbagFill />
                    My Order
                  </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="fifth">
                    <BsBoxArrowInRight />
                    Logout
                  </Nav.Link>
                </Nav.Item>
              </Nav>
            </div>
          </Col>
          <Col md={9} xs={9} lg={9} className="profile-col-2">
            <div className="side-tabs">
              <Tab.Content>
                <Tab.Pane eventKey="first">
                  <div className="profile-edit">
                    <h3
                      style={{
                        borderBottom: "1px solid #ddd",
                        paddingBottom: "15px",
                        fontWeight: "500",
                        fontSize: "24px",
                      }}
                    >
                      Profile
                    </h3>
                    <h2 className="heading-md">
                      Name, ID and Email Addresses.
                    </h2>
                    <p>
                      Below are the name and email addresses on file for your
                      account.
                    </p>
                    <br></br>
                    <Table responsive>
                                <tbody>
                                    <tr>
                                        <th >First Name</th>
                                        <td>{props.userProfile.name}</td>
                                    </tr>
                                    <tr>
                                        <th>Email Address</th>
                                        <td>{props.userProfile.email}</td>
                                    </tr>
                                    <tr>
                                        <th>Contact Number</th>
                                        <td>{props.userProfile.number}</td>
                                    </tr>
                                    <tr>
                                        <th>Address</th>
                                        <td></td>
                                    </tr>
                                </tbody>
                              </Table>
                  </div>
                </Tab.Pane>
                <Tab.Pane eventKey="second">
                          <div className="side-tabs1">
                            <h3 style={{fontSize:'25px',paddingBottom:"15px",borderBottom:"1px solid #ddd",fontWeight:"500"}}>Change Password</h3>
                            <h2 className='heading-2'>Manage Your Password</h2>
                            <p style={{color:'#666'}}>Below are the New Password and Confirm Password.</p>
                          </div>
                          <br/>
                          <Form>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>New Password</Form.Label>
                               <Form.Control type="password" size="sm"/>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>Confirm Password</Form.Label>
                               <Form.Control type="password" size="sm"/>
                            </Form.Group>
                            <div style={{textAlign:'center'}}>
                              <Button type="submit" variant="dark">change password</Button>
                            </div>
                          </Form>
                        </Tab.Pane>
                        <Tab.Pane eventKey="third">
                        <div className="side-tabs1">
                            <h3 style={{fontSize:'25px',paddingBottom:"15px",borderBottom:"1px solid #ddd",fontWeight:"500"}}>Update Profile</h3>
                            <h2 className='heading-2'>Manage Your Name, Email, Phone Number and others..</h2>
                            <p style={{color:'#666'}}>Below are the name and email addresses on file for your account.</p>
                          </div>
                          <br/>
                          <Form>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>First Name</Form.Label>
                               <Form.Control type="text" size="sm" value={props.userProfile.name}
                                 onChange={(e)=>props.setName(e.target.value)}/>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>Email</Form.Label>
                               <Form.Control type="email" size="sm" value={props.userProfile.email}
                                onChange={(e)=>props.setEmail(e.target.value)}/>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>Contact No</Form.Label>
                               <Form.Control type="text" size="sm" value={props.userProfile.number}
                                  onChange={(e)=>props.setNumber(e.target.value)}/>
                            </Form.Group>
                            <div style={{textAlign:'left'}}>
                              <Button type="submit" variant="dark">Change Phone</Button>
                            </div>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>Address</Form.Label>
                               <Form.Control type="email" size="sm"/>
                            </Form.Group>
                            <div style={{textAlign:'center'}}>
                              <Button type="submit" variant="dark">update</Button>
                            </div>
                          </Form>
                        </Tab.Pane>
                        <Tab.Pane eventKey="fourth">
                          <div className="side-tabs1">
                            <h3 style={{fontSize:'25px',paddingBottom:"15px",borderBottom:"1px solid #ddd",fontWeight:"500"}}>Order History</h3>
                            <p style={{color:'#666'}}>Below are the recent orders</p>
                          </div>
                          <div style={{textAlign:'center',fontSize:'27px'}}>
                              <p>No results found.</p>
                            </div>
                        </Tab.Pane>
              </Tab.Content>
            </div>
          </Col> 
               
            
        
        </Row>
      </Tab.Container>
        </>
    }
     
    </>
  );
}
const mapStateToProps = (state) => {
  return {
    userProfile: state.productFeature.userProfile,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    setUserProfile: (payload) => dispatch(setUserProfile(payload)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Profile);

  